___CSS_LOADER_EXPORT___.push([module.id, `/* Global styles for MUI extension */
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FAF8F5;
  color: #3A3A3A;
}

* {
  box-sizing: border-box;
}

/* Extension popup specific styles */
.extension-popup {
  width: 400px;
  min-height: 500px;
  background: #FAF8F5;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #E8DDD0;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background: #6B5B3F;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #8B6F3F;
}

/* Animation for paw prints */
@keyframes pawPrint {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.animate-paw {
  animation: pawPrint 2s ease-in-out infinite;
}

/* Highlight styles for extracted content */
mark.highlight-soulvet {
  background-color: rgba(196, 164, 132, 0.2);
  color: #8B6F3F;
  padding: 1px 2px;
  border-radius: 2px;
}`, "",{"version":3,"sources":["webpack://./src/globals.css"],"names":[],"mappings":"AAAA,oCAAoC;AACpC;EACE,SAAS;EACT,UAAU;EACV,uDAAuD;EACvD,mCAAmC;EACnC,kCAAkC;EAClC,yBAAyB;EACzB,cAAc;AAChB;;AAEA;EACE,sBAAsB;AACxB;;AAEA,oCAAoC;AACpC;EACE,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA,qBAAqB;AACrB;EACE,UAAU;AACZ;;AAEA;EACE,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,mBAAmB;AACrB;;AAEA,6BAA6B;AAC7B;EACE,WAAW,mBAAmB,EAAE;EAChC,MAAM,qBAAqB,EAAE;AAC/B;;AAEA;EACE,2CAA2C;AAC7C;;AAEA,2CAA2C;AAC3C;EACE,0CAA0C;EAC1C,cAAc;EACd,gBAAgB;EAChB,kBAAkB;AACpB","sourcesContent":["/* Global styles for MUI extension */\nbody {\n  margin: 0;\n  padding: 0;\n  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  background-color: #FAF8F5;\n  color: #3A3A3A;\n}\n\n* {\n  box-sizing: border-box;\n}\n\n/* Extension popup specific styles */\n.extension-popup {\n  width: 400px;\n  min-height: 500px;\n  background: #FAF8F5;\n}\n\n/* Custom scrollbar */\n::-webkit-scrollbar {\n  width: 6px;\n}\n\n::-webkit-scrollbar-track {\n  background: #E8DDD0;\n  border-radius: 3px;\n}\n\n::-webkit-scrollbar-thumb {\n  background: #6B5B3F;\n  border-radius: 3px;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background: #8B6F3F;\n}\n\n/* Animation for paw prints */\n@keyframes pawPrint {\n  0%, 100% { transform: scale(1); }\n  50% { transform: scale(1.1); }\n}\n\n.animate-paw {\n  animation: pawPrint 2s ease-in-out infinite;\n}\n\n/* Highlight styles for extracted content */\nmark.highlight-soulvet {\n  background-color: rgba(196, 164, 132, 0.2);\n  color: #8B6F3F;\n  padding: 1px 2px;\n  border-radius: 2px;\n}"],"sourceRoot":""}]);
